import React, { Component } from 'react';
import Scroller from 'yo3/component/scroller';
import Touchable from 'yo3/component/touchable';
import './touchEventSimulator';

import 'yo3/component/common/tapEventPluginInit';

export default class Page extends Component {

    constructor() {
        super();
    }

    render() {
        const { right, onLeftPress, noIcon, scrollable }=this.props;
        const hideHeader = location.href.search('hideHeader') !== -1;
        const hideIcon = noIcon ? true : location.href.search('hideIcon') !== -1;

        return (
            <div className="yo-flex">
                {!hideHeader ?
                    <header className="yo-header">
                        <h2 className="title">{this.props.title || "标题"}</h2>
                        {!hideIcon ?
                            <Touchable touchClass="touchable-opacity" onTap={()=> {
                                !onLeftPress ? history.go(-1) : onLeftPress();
                            }}>
                                <a className="regret yo-ico"></a>
                            </Touchable>
                            : null}
                        {!hideIcon && right ?
                            <Touchable onTap={()=> {
                                right.onPress && right.onPress();
                            }}>
                                <a href="javascript:void 0;">{right.text}</a>
                            </Touchable>
                            : null}
                    </header> : null}
                {
                    scrollable ?
                        <Scroller extraClass={this.props.extraClass ? "flex " + this.props.extraClass : "flex"}>
                            {this.props.children}
                        </Scroller> :
                        <div className={this.props.extraClass ? "flex " + this.props.extraClass : "flex"}>
                            {this.props.children}
                        </div>
                }
            </div>
        );
    }
}



