import 'babel-polyfill';
import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import 'yo3/component/common/tapEventPluginInit';
import Page from '../../common/page';
import Modal from 'yo3/component/modal';
import Popup from 'yo3/component/popup';
import Picker from 'yo3/component/picker';
import InputNumber from 'yo3/component/inputnumber';
import Touchable from 'yo3/component/touchable';



class ModalDemo extends Component {

    constructor() {
        super();
        this.state = {
            show: false,
            align: 'center',
            animation: 'fade',
            offsetX: 0,
            offsetY: -22,
            maskOffsetX: 44,
            maskOffsetY: 0,
            pickerShowStatus: {
                align: false,
                ani: false,
                width: false,
                height: false
            }
        }
    }

    togglePicker(name, status) {
        this.setState({
            pickerShowStatus: Object.assign({}, this.state.pickerShowStatus, { [name]: status })
        });
    }

    render() {
        return (
            <Page title="Modal Demo" scrollable onLeftPress={()=>location.href = './index.html'}>
                <ul className="yo-list">
                    <li className="item" onTouchTap={()=>this.togglePicker('align', true)}>
                        <div className="flex">位置(align):</div>
                        {this.state.align}
                        <i className="yo-ico more">&#xf07f;</i>
                    </li>
                    <li className="item" onTouchTap={()=>this.togglePicker('ani', true)}>
                        <div className="flex">动画(animation):</div>
                        {this.state.animation}
                        <i className="yo-ico more">&#xf07f;</i>
                    </li>
                    <li className="item">
                        <label>内容区X轴偏移(contentOffset[0]):</label>
                        <InputNumber
                            min={-200}
                            max={200}
                            step={20}
                            value={this.state.offsetX}
                            onChange={value => this.setState({ offsetX: value })}
                        />
                    </li>
                    <li className="item">
                        <label>内容区Y轴偏移(contentOffset[1]):</label>
                        <InputNumber
                            min={-200}
                            max={200}
                            step={20}
                            value={this.state.offsetY}
                            onChange={value => this.setState({ offsetY: value })}
                        />
                    </li>
                    <li className="item">
                        <label>遮罩层顶部偏移(maskOffset[0]):</label>
                        <InputNumber
                            min={0}
                            max={200}
                            step={20}
                            value={this.state.maskOffsetX}
                            onChange={value => this.setState({ maskOffsetX: value })}
                        />
                    </li>
                    <li className="item">
                        <label>遮罩层底部偏移(maskOffset[1]):</label>
                        <InputNumber
                            min={0}
                            max={200}
                            step={20}
                            value={this.state.maskOffsetY}
                            inputDisable={true}
                            onChange={value => this.setState({ maskOffsetY: value })}
                        />
                    </li>
                </ul>
                <Touchable touchClass='touchable-opacity' onTap={()=>this.setState({ show: true })}>
                    <button className="yo-btn open-modal">
                        打开弹层
                    </button>
                </Touchable>
                <Modal
                    extraClass="demo-modal"
                    onMaskTap={()=>this.setState({ show: false })}
                    show={this.state.show}
                    align={this.state.align}
                    animation={this.state.animation}
                    contentOffset={[this.state.offsetX, this.state.offsetY]}
                    maskOffset={[this.state.maskOffsetX, this.state.maskOffsetY]}
                >
                    <div style={{ height: 200, width: 200 }}>点击蒙层以关闭Modal</div>
                </Modal>
                <Popup
                    show={this.state.pickerShowStatus.align}
                    onMaskTap={()=>this.togglePicker('align', false)}
                >
                    <Picker
                        options={[{ value: 'top' }, { value: 'bottom' }, { value: 'center' }, { value: 'left' }, { value: 'right' }]}
                        value={this.state.align}
                        onChange={(opt)=>this.setState({ align: opt.value })}
                    />
                </Popup>
                <Popup
                    show={this.state.pickerShowStatus.ani}
                    onMaskTap={()=>this.togglePicker('ani', false)}
                >
                    <Picker
                        options={[
                            { value: 'fade' },
                            { value: 'zoom' },
                            { value: 'fade-in-up' },
                            { value: 'fade-in-down' },
                            { value: 'none' }
                        ]}
                        value={this.state.animation}
                        onChange={(opt)=>this.setState({ animation: opt.value })}
                    />
                </Popup>
            </Page>
        );
    }
}

ReactDOM.render(<ModalDemo />, document.getElementById('content'));


